  // Darken a color
  darkenColor(colorStr) {
    // Defined in dygraph-utils.js
    var color = Dygraph.toRGB_(colorStr);
    color.r = Math.floor((255 + color.r) / 2);
    color.g = Math.floor((255 + color.g) / 2);
    color.b = Math.floor((255 + color.b) / 2);
    return 'rgb(' + color.r + ',' + color.g + ',' + color.b + ')';
  }

  // This function draws bars for a single series. See
  // multiColumnBarPlotter below for a plotter which can draw multi-series
  // bar charts.
  barChartPlotter(e) {
    var ctx = e.drawingContext;
    var points = e.points;
    var y_bottom = e.dygraph.toDomYCoord(0);

    ctx.fillStyle = this.darkenColor(e.color);

    // Find the minimum separation between x-values.
    // This determines the bar width.
    var min_sep = Infinity;
    for (var i = 1; i < points.length; i++) {
      var sep = points[i].canvasx - points[i - 1].canvasx;
      if (sep < min_sep) min_sep = sep;
    }
    var bar_width = Math.floor(2.0 / 3 * min_sep);

    // Do the actual plotting.
    for (var j = 0; j < points.length; j++) {
      var p = points[j];
      var center_x = p.canvasx;

      ctx.fillRect(center_x - bar_width / 2, p.canvasy,
        bar_width, y_bottom - p.canvasy);

      ctx.strokeRect(center_x - bar_width / 2, p.canvasy,
        bar_width, y_bottom - p.canvasy);
    }
  }


  legendFormatter(data) {
    const g = data.dygraph
    const labels = g ? g.getLabels() : []
    if (data.x === null) {
      // This happens when there's no selection and {legend: 'always'} is set.
      return data.series
                  .map(series => 
                          '<div>' + series.dashHTML + '</div>' +
                          '<div>' + series.labelHTML + '</div>'
                        )

    }
    const nowZeroSec = new Date(); nowZeroSec.setSeconds(0); nowZeroSec.setMilliseconds(0);
    var html = '<div>' + ( 
            this.getLabels()[0] + ': ' + (data.xHTML || dateString_(nowZeroSec))
    )  + '</div>' ;
    data.series.forEach((series, i) => {
      if (!series.isVisible) return;
      var labeledData = series.labelHTML  + (series.yHTML ? ': ' + series.yHTML + "&nbsp;" : ': ------');
      if (series.isHighlighted) {
        labeledData = '<b>' + labeledData + '</b>';
      }
      var strokePattern = i>0 && g ? g.getOption('strokePattern', labels[i+1]) : [];
      const border = 
            (!strokePattern || strokePattern.length <= 1 ? "solid " : "dashed ") +
           (series.color || "white") +
           " 3px"
      html += `<div style="border-bottom: ${border}">` +labeledData+ '</div>';
    });

    return html;
  }

